import React from "react";

const WeatherCard = ({ data, darkTheme }) => {
  const { name, main, weather, wind } = data;
  const iconUrl = `https://openweathermap.org/img/wn/${weather[0].icon}@2x.png`;

  return (
    <div className={`p-6 rounded-lg shadow-md text-center transition-all ${darkTheme ? 'bg-gray-800' : 'bg-white'}`}>
      <div className="flex justify-center items-center mb-2">
        <h2 className="text-2xl font-bold">{name}</h2>
        <img src={iconUrl} alt="Weather icon" className="h-16 w-16" />
      </div>
      <p className="text-xl mb-3">{weather[0].description}</p>
      <div className="grid grid-cols-2 gap-4">
        <div className="text-center">
          <p className="text-3xl font-bold">{Math.round(main.temp)}째C</p>
          <p className="text-sm opacity-75">Feels like: {Math.round(main.feels_like)}째C</p>
        </div>
        <div className="grid grid-cols-1 gap-2">
          <p>Humidity: {main.humidity}%</p>
          <p>Wind: {wind.speed} km/h</p>
          <p>Pressure: {main.pressure} hPa</p>
        </div>
      </div>
    </div>
  );
};

export default WeatherCard;
